body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #23252c;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

::-webkit-scrollbar {
  width: 16px;
  background-color: transparent;
}

::-webkit-scrollbar-thumb {
  border-radius: 8px;
  border: transparent solid 4px;
  background-clip: content-box;
  background-color: #888888;
}

::-webkit-scrollbar-thumb:hover {
  background-color: #cecece;
}

#root {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.app {
  margin-top: 60px;
  width: 90%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
  gap: 20px;
}

button {
  position: absolute;
  top: 10px;
  height: 40px;
  width: 150px;
  font-size: large;
  font-weight: bold;
  border: none;
  border-left: 0 solid blue;
  border-right: 0 solid blue;
  border-radius: 8px;
  background-color: #3c3c3c;
  color: white;
  cursor: pointer;
  transition: all 0.1s linear;
}

button:hover {
  border-left: 3px solid blue;
  border-right: 3px solid blue;
}

button:active {
  color: blue;
}

.card {
  height: 370px;
  width: 280px;
  background-color: transparent;
  color: white;
}
.card .card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 0.8s;
  transform-style: preserve-3d;
}
.card .card-inner hr {
  height: 1px;
  width: 100%;
  border: none;
  margin: 0;
  margin-bottom: 5px;
  background-color: white;
}
.card .card-inner .card-front,
.card .card-inner .card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 15px;
  background-color: #1a1b21;
  backface-visibility: hidden;
  overflow: hidden;
}
.card .card-inner .card-front img {
  width: 100%;
  background-color: bisque;
}
.card .card-inner .card-front h3 {
  margin: 10px;
}
.card .card-inner .card-front p {
  margin: 0;
  text-align: center;
  color: #9f9f9f;
}
.card .card-inner .card-back {
  transform: rotateY(180deg);
  height: calc(100% - 10px);
  width: 90%;
  padding: 0 5% 10px 5%;
  overflow: overlay;
}
.card .card-inner .card-back .basic-info {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
}
.card .card-inner .card-back .basic-info img {
  width: 40px;
  border-radius: 50%;
  background-color: bisque;
}
.card .card-inner .card-back .more-info {
  width: 100%;
}
.card .card-inner .card-back .more-info table {
  width: 100%;
}
.card .card-inner .card-back .more-info table td:first-child {
  text-align: start;
}
.card .card-inner .card-back .more-info table td:last-child {
  word-break: break-all;
  text-align: end;
}
.card .card-inner .card-back .more-info table td:last-child a {
  color: CornflowerBlue;
}

.card:hover .card-inner {
  transform: rotateY(180deg);
}

